.navbar {
  display: flex;
  gap: 10px;
  background: #333;
  padding: 10px;
  flex-wrap: wrap;
}

.nav-item {
  color: white;
  padding: 8px 15px;
  cursor: pointer;
  border-radius: 5px;
  transition: 0.3s;
}

.nav-item:hover {
  background: #555;
}

.nav-item.active {
  background: #f57c00;
}
